@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

.main-container {
    display: grid;
    grid-template-columns: 70% 30%;
}

.restaurant-form {
    position: absolute;
    z-index: 15;
    background-color: #fff;
    padding: 2rem 2% 1rem 2%;
    top: 5vh;
    left: 20vw;
    width: 35%;
    transition: all 0.4s linear;
    visibility: visible;
    opacity: 1; 
    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);
}

.restaurant-form label,.restaurant-form input {
    display: block;
    margin-bottom: 1rem;
    width: 90%;
    border-radius: 0;
}

.restaurant-form input {
    border: 1px solid rgb(151, 151, 151);
    padding: 2%;
    outline: rgb(112, 18, 18);
}

.restaurant-form-modal-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    
    background-color: rgba(20, 20, 20, 0.171);
    z-index: 10;
}

.restaurant-form-hidden {
    display: none;
}

.restaurant-container {
    padding: 1rem 2rem;
    font-weight: 300;
    max-height: calc(100vh - 2rem);
    overflow-y: scroll;
}

.range-slider  {
    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);

    padding: 1rem 1rem;
    margin: 1rem 0;
}

.range-slider h2{
    padding-bottom: 1rem;
}

.range-slider select{
    margin: 0.5rem 0.5rem;
    padding: 0.3rem;
    border-radius: 0;
    outline: none;
}



.restaurant {
    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);

    padding: 1rem 0.5rem;
    margin: 1rem 0;
}

.restaurant h2, .restaurant h4 {
    margin: 1rem 2% 1rem 2%;
    max-width: 96%;
}

.restaurant button {
    display: inline-block;
    margin: 0 2% 1rem 2%;
    padding: 0.6rem;
    border-radius: 0;
    border: 2px solid rgb(156, 156, 156);
    background-color: #fff;
    font-size: 1rem;
    transition: all 0.3s ease-in-out;
    outline: none;
}

.restaurant button:hover {
    border-radius: 0;
    color: #fff;
    border: 2px solid rgb(156, 156, 156);
    background-color: rgb(156, 156, 156);
    cursor: pointer;
}

.modal-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    
    background-color: rgba(20, 20, 20, 0.171);
    z-index: 10;
}

.modal {
    position: absolute;
    z-index: 15;
    background-color: #fff;
    padding: 2rem 2% 1rem 2%;
    top: 5vh;
    left: 20vw;
    width: 35%;
    transition: all 0.4s linear;
    visibility: visible;
    opacity: 1; 
    max-height: 85vh;
    overflow-y: scroll;

    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);
}

.modal img {
    max-width: 100%;
    margin: 0 0 2rem 0;
}

.modal h2 {
    margin: 0 0 1rem 0;
    font-weight: 300;
    font-size: 2rem;
}

.modal button {
    margin-left: 0;
    padding:0.5rem;
    border-radius: 0;
    border: 1px solid rgb(151, 151, 151);
    transition: all 0.2s linear;
    outline: none;
}

.modal button:hover,.modal button:active {
    cursor: pointer;
    color: #fff;
    background-color: rgb(151, 151, 151);
    border: 1px solid rgb(151, 151, 151);
}

.modal p {
    margin: 1rem 0 2rem 0;
    padding: 1rem 1rem 1rem 1rem;
    word-wrap: break-word;
    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.2),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);
    
}

.modal p span {
    display: block;
    margin-top: 1rem;
    text-align: right;
    font-style: italic;
}

.modal-hidden {
    opacity: 0;
    visibility: hidden;
}

.modal-close {
    position: absolute;
    right: 2%;
    top: 2rem;
}

.modal-close:hover {
    cursor: pointer;
}

.review-form {
    padding: 1rem;
    box-shadow: 
    0 1px 2px rgba(105, 105, 105, 0.07),
    0 2px 4px rgba(105, 105, 105, 0.07),
    0 4px 8px rgba(105, 105, 105, 0.07),
    0 8px 16px rgba(105, 105, 105, 0.2),
    0 16px 32px rgba(105, 105, 105, 0.07),
    0 16px 32px rgba(105, 105, 105, 0.07);
}

.review-form label,.review-form input,.review-form textarea {
    display: block;
    margin-bottom: 1rem;
    width: 90%;
    border-radius: 0;
}

.review-form input,.review-form textarea {
    border: 1px solid rgb(151, 151, 151);
    padding: 2%;
    outline: rgb(112, 18, 18);
}

.review-form textarea {
    resize: none;
}

.review-form-hidden {
    display: none;
}

.review-form .form-submit-button, .restaurant-form .form-submit-button {
    width: 50%;
    margin-left: 0;
    padding:0.5rem;
    border-radius: 0;
    background-color: #fff;
    border: 1px solid rgb(151, 151, 151);
    transition: all 0.2s linear;
}

.review-form .form-submit-button:hover, .restaurant-form .form-submit-button:hover {
    cursor: pointer;
    color: #fff;
    background-color: rgb(151, 151, 151);
    border: 1px solid rgb(151, 151, 151);
}